<template>
  <div>
    <div class="isloading" v-if="isloading">
      <i class="el-icon-loading"></i>
      <span>加载中...</span>
    </div>
    <div id="index" v-else>
      <section>
        <div class="item" v-for="blog in blogs">
          <figure class="author">
            <router-link :to="`/userblog/${blog.user.id}`">
              <img  class="img" :src="blog.user.avatar" :alt="blog.user.username">
            </router-link>
            <router-link :to="`/userblog/${blog.id}`">
              <figcaption>{{blog.user.username}}</figcaption>
            </router-link>
          </figure>
          <router-link :to="{name:'Article',params:{blogId:blog.id}}">
            <!--      <router-link :to="`/article/${blog.id}`">    -->
            <h3>{{blog.title}}<span class="time">{{friendlyDate(blog.createdAt)}}</span></h3>
            <p>
              {{blog.description}} ...
            </p>
          </router-link>
        </div>
      </section>
      <section>
        <div class="block">
          <span class="demonstration"></span>
          <el-pagination
            @current-change="handleCurrentChange"
            :page-size="10"
            layout="total, prev, pager, next"
            :total="total">
          </el-pagination>
        </div>
      </section>
    </div>
  </div>

</template>

<script>
  import blog from "../api/blog";
    export default {
      name: "Index",
      data(){
        return {
          isloading: true,
          blogs:[],
          total:1,
          time:''
        }
      },
      created() {
          blog.getAllbloglist().then(res=>{
            this.blogs = res.data;
            this.total = res.total;
            this.isloading = false;
          });
      },
      methods:{
        handleCurrentChange(newpage){
          blog.getAllbloglist({page:newpage}).then(res=>{
            this.blogs = res.data;
          })
        }
        }
    }
</script>

<style scoped>
  #index{
    margin: 20px 10px;
  }
  .item{
    display: grid;
    grid: auto auto / 80px 1fr;
    /*grid：2行/2列，第一列80px，第二列撑开 */
    grid-template-areas: 'author h3'
                          'author p';
    margin-bottom: 15px;
    padding-bottom: 10px;
    border-bottom: 1px solid #cccccc;
  }
  .author{
    grid-area: author;
    text-align: center;
    align-self: center;
    color: #666666;
  }
  .author a{
    color: #666666;
  }
  .item h3{
    grid-area: h3;
    align-self: center;
    margin-left: 20px;
    padding-bottom: 10px;
    font-size: 21px;
    font-weight: 400;
    color: #242d08;
  }
  .item .time{
    margin-left: 50px;
    color: #666666;
    font-size: 14px;
  }
  .item p{
    grid-area: p;
    margin-left: 20px;
    color: #242d08;
  }
.img{
  display:inline-block;
  width: 75px;
  height: 75px;
  border: 1px solid #aeca4f;
  border-radius: 50%;
}
  a{
    text-decoration: none;
  }
  /deep/ .el-pager li.active {
    color: #409F08;
  }
  /deep/ .el-pager li:hover{
    color: #409F08;
  }
  /deep/ .el-pagination button:hover{
    color: #409F08;
  }
  /deep/ .el-pagination{
    text-align: center;
  }
</style>



